// import '../scss/style.scss';

const faqItem = document.querySelectorAll('.faq-list-item');

const app = () => {

    faqItem.forEach(item => {
        item.addEventListener('click', e => {
            console.log(item.classList)
            if (item.classList.contains('active')) {
                item.classList.remove('active')
                item.classList.remove('font-weight-bold');
            } else {
                faqItem.forEach(itemToClose => {
                    itemToClose.classList.remove('active');
                    itemToClose.classList.remove('font-weight-bold')
                });
                item.classList.add('active');
                item.classList.add('font-weight-bold');
            }
        });
    });
}

app();